.place {
  &__photo-container {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    max-height: 256px;
    overflow: hidden;
    margin-bottom: 16px;

    @include hover (transform, scale(1.02));

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onDesktop {
      margin-bottom: 24px;
      height: 256px;
      padding-bottom: 0;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 8px;

    object-fit: cover;
    object-position: center;
  }

  // &__container {
  //   margin-bottom: 16px;

  //   @include onDesktop {
  //     margin-bottom: 24px;
  //   }
  // }

  &__link {
    font-weight: bold;
    color: $colorBlue;
    text-decoration: none;
  }
}
